*{
  margin: 0;
  background: #000;
}

.all{
  display: flex; 
  background: #000;
  height: 100vh;
}

header{
  padding: 30px 0;
  width: 20%;
}

header a{
  color: rgb(221, 221, 221);
  text-decoration: none;
  font-size: 12px;
  margin: 30px;
  display: flex;
  justify-content: left;
  align-items: center;
}

header a:nth-child(1){
  font-size: 20px;
  font-weight: 600;
  padding-bottom: 30px;
}

.black_text {
  margin: 30px 0;
}

.black_text p{
  color: rgb(219, 219, 219);
  font-size: 11px;
  margin: 5px 0;
  padding-left: 10px;
}

.black_text_2 {
  position: absolute;
  bottom: 33%;
  left: 56%;
}

.black_text_2 p{
  color: rgb(219, 219, 219);
  font-size: 12px;
  padding-bottom: 5px;
  padding-left: 0px;
}

figure{
  display: flex;
  flex-shrink: 0;
}

figure img{
  width: 100%;
  height: 100%;
}

figure img:nth-child(2){
  margin-left: 100px;
  z-index: 0;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
